.App {
  font-family: "Lato", serif;
  font-size: 1.2em;
}

.App .App-paragraph {
  display: inline-block;
  color: #3D3D3D;
}

.App .App-noun,
.App .App-paragraph {
  margin-top: 0;
  margin-bottom: 40px;
  -webkit-animation: 0.3s fadeinup cubic-bezier(0.78, 0.1, 0.19, 0.91);
          animation: 0.3s fadeinup cubic-bezier(0.78, 0.1, 0.19, 0.91);
  opacity: 0;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.App .App-noun:first-child,
.App .App-paragraph:first-child {
  -webkit-animation-delay: 0;
          animation-delay: 0;
}

.App .App-noun:nth-child(2),
.App .App-paragraph:nth-child(2) {
  -webkit-animation-delay: .1s;
          animation-delay: .1s;
}

.App .App-noun:nth-child(3),
.App .App-paragraph:nth-child(3) {
  -webkit-animation-delay: .2s;
          animation-delay: .2s;
}

.App .App-noun:nth-child(4),
.App .App-paragraph:nth-child(4) {
  -webkit-animation-delay: .3s;
          animation-delay: .3s;
}

.App .App-noun:nth-child(5),
.App .App-paragraph:nth-child(5) {
  -webkit-animation-delay: .4s;
          animation-delay: .4s;
}

.App .App-noun:nth-child(6),
.App .App-paragraph:nth-child(6) {
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}

.App .App-noun:nth-child(7),
.App .App-paragraph:nth-child(7) {
  -webkit-animation-delay: .6s;
          animation-delay: .6s;
}

.App .App-noun:nth-child(8),
.App .App-paragraph:nth-child(8) {
  -webkit-animation-delay: .7s;
          animation-delay: .7s;
}

.App .App-noun:nth-child(9),
.App .App-paragraph:nth-child(9) {
  -webkit-animation-delay: .8s;
          animation-delay: .8s;
}

.App .App-noun:nth-child(10),
.App .App-paragraph:nth-child(10) {
  -webkit-animation-delay: .9s;
          animation-delay: .9s;
}

.App .App-noun:last-child,
.App .App-paragraph:last-child {
  -webkit-animation: 4s levitate 1s cubic-bezier(0.78, 0.1, 0.19, 0.91);
          animation: 4s levitate 1s cubic-bezier(0.78, 0.1, 0.19, 0.91);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.App .App-noun {
  display: inline-block;
}

.App .App-noun.App-noun--red {
  color: #E4572E;
}

.App .App-noun.App-noun--green {
  color: #76B041;
}

.App .App-noun.App-noun--blue {
  color: #17BEBB;
}

.App .App-noun.App-noun--purple {
  color: #995D81;
}

.App .App-wrap {
  padding: 60px;
}

.App .App-catch-wrap {
  width: 100%;
  height: 300px;
  position: absolute;
  left: 0;
  bottom: 0;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  transition: .3s;
  z-index: 2;
}

.App .App-catch-wrap .App-catch-wrap-social {
  text-decoration: none;
  position: absolute;
  color: #F18805;
  -webkit-animation: .3s fadeinup .2s ease-in-out;
          animation: .3s fadeinup .2s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  bottom: 60px;
  opacity: 0;
  -webkit-transform: translateX(-50%) translateY(-50%);
      -ms-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}

.App .App-catch-wrap .App-catch-wrap-social:nth-child(6) {
  right: calc(60px);
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
}

.App .App-catch-wrap .App-catch-wrap-social:nth-child(5) {
  right: calc(100px);
  -webkit-animation-delay: 1.4s;
          animation-delay: 1.4s;
}

.App .App-catch-wrap .App-catch-wrap-social:nth-child(4) {
  right: calc(140px);
  -webkit-animation-delay: 1.3s;
          animation-delay: 1.3s;
}

.App .App-catch-wrap .App-catch-wrap-social:nth-child(3) {
  right: calc(180px);
  -webkit-animation-delay: 1.2s;
          animation-delay: 1.2s;
}

.App .App-catch-wrap .App-catch-wrap-social:nth-child(2) {
  right: calc(220px);
  -webkit-animation-delay: 1.1s;
          animation-delay: 1.1s;
}

.App .App-catch-wrap .App-catch-wrap-social:nth-child(1) {
  right: calc(260px);
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

@-webkit-keyframes fadeinup {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fadeinup {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes levitate {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  30% {
    opacity: 1;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@keyframes levitate {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  30% {
    opacity: 1;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
}

@-webkit-keyframes fadeoutdown {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}

@keyframes fadeoutdown {
  from {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  to {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
}



